import React from 'react'

function Detalle ({
    active
}) {
    return (
        <div className={`Detalle ${active ? 'active':''}`}>
            <img className="video" src="/BITT_02.jpg" />
            <img className="play-icon" src="/play-icon.svg" />
            <style jsx>
                {`
                    .Detalle {
                        position: absolute;
                        top: 100%;
                        max-width: 100%;
                        width: 100vw
                    }
                    .Detalle .video {
                        width: 100%;
                    }
                    .Detalle .play-icon {
                        position: absolute;
                        width: 10%;
                        height: 10%;
                        top: 45%;
                        left: 45%;
                        cursor: pointer;
                    }
                `}
            </style>
        </div>
    )
}

export default Detalle;